<template>
    <div>
        <el-form ref="form" :model="form" label-width="110px" :rules="rules">
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="用户名称" prop="user_name">
                        <el-input v-model="form.user_name"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="user_pass">
                        <el-input type="password" v-model="form.user_pass"></el-input>
                    </el-form-item>
                    <el-form-item label="重复密码" prop="check_pass">
                        <el-input type="password" v-model="form.check_pass"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名" prop="user_real_name">
                        <el-input v-model="form.user_real_name"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话">
                        <el-input v-model="form.user_phone"></el-input>
                    </el-form-item>
                    <el-form-item label="岗位" prop="pos_id">
                        <el-select v-model="form.pos_id" placeholder="请选择" class="mr10">
                            <el-option
                                v-for="item in positionOptions"
                                :key="item.id"
                                :label="item.position_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="所属公司" prop="cp_id">
                        <el-tree
                            v-loading="loading"
                            ref="domTreeCompany"
                            :data="dataTreeCompany"
                            show-checkbox
                            check-strictly
                            highlight-current
                            node-key="id"
                            @check-change="handleCheckChange"
                            @node-click="handleNodeClick"
                            default-expand-all
                            :expand-on-click-node="false">
                            <span class="custom-tree-node" slot-scope="{ node, data }">
                                <span>{{ data.mc_name }}</span>
                            </span>
                        </el-tree>
                    </el-form-item>
                    <el-form-item label="数据权限" prop="user_cp_id">
                        <el-tree
                            v-loading="loading"
                            ref="domTreePermission"
                            :data="dataTreePermission"
                            show-checkbox
                            check-strictly
                            node-key="id"
                            @check="handleClickPermission"
                            default-expand-all
                            :expand-on-click-node="false">
                            <span class="custom-tree-node" slot-scope="{ node, data }">
                                <span>{{ data.mc_name }}</span>
                            </span>
                        </el-tree>
                    </el-form-item>
                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit">保 存</el-button>
                <el-button @click="$router.back()">返 回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/enterpriseManagement'
    export default {
        data: function(){
            const data = [];
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.form.user_pass) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                collapseActive: ['1'],
                pageType: '',
                loading: false,
                positionOptions: [],
                dataTreeCompany: JSON.parse(JSON.stringify(data)),
                dataTreePermission: JSON.parse(JSON.stringify(data)),
                form: {
                    company: "",
                    cp_id: [],
                    user_cp_id: [],
                    pos_id: "",
                    pos_name: "",
                    user_auth: "",
                    user_id: "",
                    user_name: "",
                    user_pass: "",
                    check_pass: "",
                    user_phone: "",
                    user_real_name: ""
                },
                rules: {
                    user_name: [
                        { required: true, message: '请输入用户名称', trigger: 'blur' }
                    ],
                    user_pass: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ],
                    check_pass: [
                        {required: true, validator: validatePass2, trigger: 'blur' }
                    ],
                    user_real_name: [
                        { required: true, message: '请输入姓名', trigger: 'blur' }
                    ],
                    pos_id: [
                        { required: true, message: '请选择岗位', trigger: 'blur' }
                    ],
                    company: [
                        { required: true, message: '请勾选所属公司', trigger: 'blur' }
                    ]
                }
            }
        },
        mounted(){
            this.getPositionList()
            this.checkPath();
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData()
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.getFormData()
                    this.pageType = 'view';
                }
                this.getTreeList();
            },
            getFormData(){
                API.userManagementView({id: this.$route.query.id}).then(res => {
                    if(res.code == 0){
                        Object.assign(this.form,res.data)
                        this.$refs.domTreeCompany.setCheckedKeys(res.data.cp_id);
                        this.$refs.domTreePermission.setCheckedKeys(res.data.user_cp_id);
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getTreeList(){
                this.loading = true
                API.organizationList({id: this.$route.query.id}).then(res => {
                    this.loading = false
                    if(res.code == 0){
                        this.dataTreeCompany = JSON.parse(JSON.stringify(res.data))
                        this.dataTreePermission = JSON.parse(JSON.stringify(res.data))
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getPositionList(){
                API.userPositionList({}).then(res => {
                    if(res.code == 0){
                        this.positionOptions = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleClickCompany(item, items){
                
            },
            handleClickPermission(item, items){
                this.form.user_cp_id = items.checkedKeys
            },
            handleCheckChange(item, node, self){
                if(node==true){
                    this.form.cp_id = [item.id]
                    this.$refs.domTreeCompany.setCheckedKeys([item.id])
                }else{
                    if(this.form.cp_id == [item.id]){
                        this.$refs.domTreeCompany.setCheckedKeys([item.id])
                    }
                }
            },
            handleNodeClick(item, node, self){
                this.form.cp_id = [item.id]
                this.$refs.domTreeCompany.setCheckedKeys([item.id])
            },
            handleSubmit() {
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        if(this.pageType == 'add'){
                            API.userManagementAdd(this.form).then(res => {
                                if(res.code == 0){
                                    this.$message.success('提交成功！');
                                    this.$router.back()
                                }else{
                                    this.$message.error(res.msg)
                                }
                                
                            })
                        }else{
                            let params = {
                                id:this.$route.query.id,
                                ...this.form
                            }
                            API.userManagementEdit(params).then(res => {
                                if(res.code == 0){
                                    this.$message.success('提交成功！');
                                    this.$router.back()
                                }else{
                                    this.$message.error(res.msg)
                                }
                                
                            })
                        }
                    }else{
                        return false
                    }
                })
                
                
                
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>